import React, { useEffect, useRef, useState } from 'react';
import './TextField.css';

const TextField = ({
  placeholder = '',
  showHint = false,
  hint = '',
  autofocus = false,
  maxLength: number,
  minLength,
  value: initialValue = '',
  onChange,
  onInput,
  disabled = false,
  theme = 'light-theme',
}) => {
  const [value, setValue] = useState(initialValue);
  const inputRef = useRef(null);

  useEffect(() => {
    if (autofocus && inputRef.current) {
      inputRef.current.focus();
    }

    if (showHint && inputRef.current) {
      const hintContainer = document.getElementById('hint-container');
      if (hintContainer) {
        const hintWidth = hintContainer.getBoundingClientRect().width;
        inputRef.current.style.paddingRight = `${Math.round(hintWidth)}px`;
      }
    }
  }, [autofocus, showHint]);

  const handleInputChange = (e) => {
    const newValue = e.target.value;
    if (onInput) {
      onInput(newValue);
    }
    setValue(newValue);
  };

  const handleBlur = () => {
    if (onChange) {
      onChange(value);
    }
  };

  const getClasses = () => {
    const classes = [`input--${theme}`];
    if (disabled) {
      classes.push(`input--disabled--${theme}`, 'input--disabled');
    }
    return classes.join(' ');
  };

  const makeInvalid = () => {
    if (inputRef.current) {
      inputRef.current.classList.add('error');
      if (theme === 'dark-theme') {
        inputRef.current.classList.add('error--dark');
      }
    }
  };

  return (
    <div className="text-field-container">
      <input
        ref={inputRef}
        type="text"
        placeholder={placeholder}
        maxLength={maxLength}
        minLength={minLength}
        value={value}
        onChange={handleInputChange}
        onBlur={handleBlur}
        disabled={disabled}
        className={getClasses()}
      />
      {showHint && <div id="hint-container" className="hint">{hint}</div>}
    </div>
  );
};

export default TextField;
